/*========== Basic ==========*/
html {
  font-size: 16px;
  height: 100%; }

body {
  font-family: 'ProximaNova', sans-serif;
  color: #fff;
  font-size: 1rem;
  line-height: 1.5;
  min-width: 320px;
  pointer-events: none;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out, background-color 0.3s ease-in-out;
  height: 100%;
  overflow: hidden; }
  body.loaded {
    opacity: 1 !important;
    visibility: visible !important;
    pointer-events: all !important;
    background-color: #0a0a0d; }

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none; }
  *:before, *:after {
    box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

/*========== Layout ==========*/
.wrapper {
  height: 100%;
  position: relative; }
  .wrapper__content {
    height: 100%;
    position: relative;
    z-index: 2;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch; }
  .wrapper__background {
    pointer-events: none;
    position: fixed;
    width: 580px;
    height: 100%;
    left: 50%;
    top: 0;
    transform: translateX(-50%); }
    .wrapper__background img {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .wrapper__background::before, .wrapper__background::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      width: 60px;
      height: 100%;
      top: 0; }
    .wrapper__background::before {
      left: 0;
      background: linear-gradient(-90deg, rgba(10, 10, 13, 0) 0%, #0a0a0d 100%); }
    .wrapper__background::after {
      right: 0;
      background: linear-gradient(90deg, rgba(10, 10, 13, 0) 0%, #0a0a0d 100%); }

.screen {
  margin: 0 auto;
  padding: 20px 0; }
  .screen__container {
    margin: 0 auto;
    max-width: 580px;
    padding: 0 15px; }

/*========== Components ==========*/
.screen {
  position: relative;
  min-height: 100%;
  height: 1000px; }
